import React, { useCallback, useEffect, useState } from 'react';

import {
  Box,
  Button,
  Divider,
  Grid,
  FormControl,
  FormControlLabel,
  RadioGroup,
  Radio,
  Paper,
  TextField,
  Typography,
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableCell,
  ToggleButtonGroup,
  ToggleButton,
  InputAdornment,
  Select,
  MenuItem,
  Checkbox,
} from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import { ThemeProvider } from '@mui/material/styles';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { DesktopDatePicker } from '@mui/x-date-pickers/DesktopDatePicker';

import dayjs from 'dayjs';

import { useAlert } from '../../hooks';

import { searchbuttontheme, initbuttontheme, sx } from './styles';

const ScheduleManageScreen = () => {
  console.log('ScheduleManageScreen');
  const DAYJS_FORMAT = 'YYYY.MM.DD';

  const { show, hide } = useAlert();

  const [totalSchedule, setTotalSchedule] = useState(0);
  const [closedSchedule, setClosedSchedule] = useState(0);
  const [searchOption, setSearchOption] = useState('host');
  const [searchKeyword, setSearchKeyword] = useState('');
  const [leisure, setLeisure] = useState('');
  const [peopleCount, setPeopleCount] = useState('');
  const [period, setPeriod] = useState('today');
  const [startDate, setStartDate] = useState(dayjs().format(DAYJS_FORMAT));
  const [endDate, setEndDate] = useState(dayjs().format(DAYJS_FORMAT));

  const [checked, setChecked] = useState([]);
  const [scheduleListOption, setScheduleListOption] = useState('');

  // axios (/leisure)에서 가져와서 채워야함
  const leisures = [
    { value: 'golf', title: '골프' },
    { value: 'fishing', title: '낚시' },
    { value: 'basket', title: '농구' },
    { value: 'hiking', title: '등산' },
  ];

  const peopleCounts = [
    { value: '5', title: '1~5명' },
    { value: '10', title: '6~10명' },
  ];

  const periods = [
    { value: 'today', title: '오늘' },
    { value: 'week', title: '1주일' },
    { value: 'month', title: '1개월' },
    { value: 'three_month', title: '3개월' },
    { value: 'six_month', title: '6개월' },
    { value: 'year', title: '1년' },
    { value: 'all', title: '전체' },
  ];

  const scheduleListOptions = [
    { value: 'normal', title: '일반 일정 목록 나열' },
    { value: 'recommend', title: '추천 일정 목록 나열' },
  ];

  useEffect(() => {
    setTotalSchedule(0);
    setClosedSchedule(0);
    setChecked([]);
  }, []);

  const onConfirmAlert = useCallback(() => {
    hide();
  }, [hide]);

  const showErrorMessage = useCallback(
    ({ message = '' }) => {
      show({
        content: message,
        label: '확인',
        onConfirm: () => onConfirmAlert(),
      });
    },
    [show, onConfirmAlert],
  );

  const handleSearchOption = event => {
    setSearchOption(event.target.value);
  };

  const handleSearchKeyword = event => {
    setSearchKeyword(event.target.value);
  };

  const handleLeisure = event => {
    setLeisure(event.target.value);
  };

  const handlePeopleCount = event => {
    setPeopleCount(event.target.value);
  };

  const handlePeriod = (event, newPeriod) => {
    setPeriod(newPeriod);
  };

  const handleStartDate = newDate => {
    if (newDate.isAfter(endDate)) {
      showErrorMessage({ message: '입력한 시작 날짜가 끝 날짜보다 느립니다.' });
    } else {
      setStartDate(newDate);
    }
  };

  const handleEndDate = newDate => {
    if (newDate.isBefore(startDate)) {
      showErrorMessage({ message: '입력한 끝 날짜가 시작 날짜보다 빠릅니다.' });
    } else {
      setEndDate(newDate);
    }
  };

  const handleSearch = () => {
    console.log('search');
  };

  const handleInitOptions = () => {
    console.log(startDate, endDate);
    setSearchOption('host');
    setSearchKeyword('');
    setLeisure('');
    setPeopleCount('');
    setPeriod('today');
    setStartDate(dayjs().format(DAYJS_FORMAT));
    setEndDate(dayjs().format(DAYJS_FORMAT));
  };

  const handleCheckedDelete = () => {
    console.log(checked);
  };

  const handleCheckedRecommend = () => {
    console.log(checked);
  };

  const handleScheduleListOption = event => {
    setScheduleListOption(event.target.value);
  };

  return (
    <>
      <Grid container sx={sx.grid}>
        <Grid item xs={12}>
          <Paper sx={sx.paper}>
            <Typography sx={sx.typotitle}>일정 관리</Typography>
            <Divider />
            <Grid container spacing={1}>
              <Grid item xs={2}>
                <Typography sx={sx.typoschedule}>전체 일정</Typography>
              </Grid>
              <Grid item xs={2}>
                <Typography sx={sx.typoschedule}>마감된 일정</Typography>
              </Grid>
            </Grid>
            <Grid container spacing={1}>
              <Grid item xs={2}>
                <Typography sx={sx.typoschedulecount}>
                  {totalSchedule}
                </Typography>
              </Grid>
              <Grid item xs={2}>
                <Typography sx={sx.typoschedulecount}>
                  {closedSchedule}
                </Typography>
              </Grid>
            </Grid>
          </Paper>
        </Grid>
      </Grid>
      <Grid container sx={sx.grid}>
        <Grid item xs={12}>
          <Paper sx={sx.paper}>
            <Grid container spacing={1}>
              <Grid item xs={1}>
                <Typography sx={sx.typoschedulesearch}>검색</Typography>
              </Grid>
              <Grid item xs={2}>
                <FormControl>
                  <RadioGroup
                    value={searchOption}
                    onChange={handleSearchOption}
                  >
                    <FormControlLabel
                      value="host"
                      control={<Radio />}
                      label="호스트 닉네임"
                    />
                    <FormControlLabel
                      value="schedule"
                      control={<Radio />}
                      label="일정 제목"
                    />
                  </RadioGroup>
                </FormControl>
              </Grid>
              <Grid item xs={6}>
                <TextField
                  margin="normal"
                  variant="standard"
                  InputProps={{
                    disableUnderline: true,
                    placeholder: '검색어를 입력하세요.',
                    startAdornment: (
                      <InputAdornment position="start">
                        <SearchIcon sx={sx.searchicon} />
                      </InputAdornment>
                    ),
                  }}
                  size="small"
                  sx={sx.search}
                  value={searchKeyword}
                  onChange={handleSearchKeyword}
                />
              </Grid>
            </Grid>
            <Grid container spacing={1}>
              <Grid item xs={1}>
                <Typography sx={sx.typoschedulesearch}>레저</Typography>
              </Grid>
              <Grid item xs={2}>
                <FormControl size="small">
                  <Select
                    displayEmpty
                    value={leisure}
                    onChange={handleLeisure}
                    sx={sx.select}
                  >
                    <MenuItem disabled value="">
                      레저 검색
                    </MenuItem>
                    {leisures.map(e => (
                      <MenuItem key={e.value} value={e.value}>
                        {e.title}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
              <Grid item xs={1}>
                <Typography sx={sx.typoschedulesearch}>인원</Typography>
              </Grid>
              <Grid item xs={2}>
                <FormControl size="small">
                  <Select
                    displayEmpty
                    value={peopleCount}
                    onChange={handlePeopleCount}
                    sx={sx.select}
                  >
                    <MenuItem disabled value="">
                      인원 검색
                    </MenuItem>
                    {peopleCounts.map(e => (
                      <MenuItem key={e.value} value={e.value}>
                        {e.title}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
            </Grid>
            <Grid container spacing={1}>
              <Grid item xs={1}>
                <Typography sx={sx.typoschedulesearch}>기간</Typography>
              </Grid>
              <Grid item xs={10}>
                <ToggleButtonGroup
                  exclusive
                  value={period}
                  onChange={handlePeriod}
                  sx={sx.togglegroup}
                >
                  {periods.map(e => (
                    <ToggleButton key={e.value} value={e.value} sx={sx.toggle}>
                      {e.title}
                    </ToggleButton>
                  ))}
                </ToggleButtonGroup>
                <LocalizationProvider dateAdapter={AdapterDayjs}>
                  <DesktopDatePicker
                    inputFormat="YYYY.MM.DD"
                    value={startDate}
                    onChange={handleStartDate}
                    renderInput={params => (
                      <TextField
                        {...params}
                        disabled
                        size="small"
                        sx={sx.typocalendartextfield}
                      />
                    )}
                  />
                  <Typography sx={sx.typocalendar}>~</Typography>
                  <DesktopDatePicker
                    inputFormat="YYYY.MM.DD"
                    value={endDate}
                    onChange={handleEndDate}
                    renderInput={params => (
                      <TextField
                        {...params}
                        disabled
                        size="small"
                        sx={sx.typocalendartextfield}
                      />
                    )}
                  />
                </LocalizationProvider>
              </Grid>
            </Grid>
            <Grid container spacing={1}>
              <Grid display="flex" justifyContent="center" item xs={12}>
                <ThemeProvider theme={searchbuttontheme}>
                  <Button
                    sx={sx.button}
                    variant="contained"
                    disabled={false}
                    onClick={handleSearch}
                  >
                    검색
                  </Button>
                </ThemeProvider>
                <ThemeProvider theme={initbuttontheme}>
                  <Button
                    sx={sx.button}
                    variant="contained"
                    disabled={false}
                    onClick={handleInitOptions}
                  >
                    초기화
                  </Button>
                </ThemeProvider>
              </Grid>
            </Grid>
          </Paper>
        </Grid>
      </Grid>
      <Grid container sx={sx.grid}>
        <Grid item xs={12}>
          <Paper sx={sx.paper}>
            <Typography sx={sx.typotitle}>일정 목록</Typography>
            <Box sx={sx.tablebuttonbox}>
              <Button
                sx={sx.tablebutton}
                variant="outlined"
                disabled={checked.length === 0}
                onClick={handleCheckedDelete}
              >
                선택 삭제
              </Button>
              <Button
                sx={sx.tablebutton}
                variant="outlined"
                disabled={checked.length === 0}
                onClick={handleCheckedRecommend}
              >
                선택 추천 일정 설정
              </Button>
              <FormControl size="small">
                <Select
                  displayEmpty
                  value={scheduleListOption}
                  onChange={handleScheduleListOption}
                  sx={sx.selectschedule}
                >
                  <MenuItem disabled value="">
                    일정 목록 변경
                  </MenuItem>
                  {scheduleListOptions.map(e => (
                    <MenuItem key={e.value} value={e.value}>
                      {e.title}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Box>
            <Divider />
            <TableContainer>
              <Table sx={sx.table}>
                <TableHead sx={sx.tableheaderrow}>
                  <TableRow>
                    <TableCell align="center" sx={sx.tableheader}>
                      <Checkbox />
                    </TableCell>
                    <TableCell align="center" sx={sx.tableheader}>
                      등록시간
                    </TableCell>
                    <TableCell align="center" sx={sx.tableheader}>
                      일정번호
                    </TableCell>
                    <TableCell align="center" sx={sx.tableheader}>
                      호스트 닉네임
                    </TableCell>
                    <TableCell align="center" sx={sx.tableheader}>
                      일정 제목
                    </TableCell>
                    <TableCell align="center" sx={sx.tableheader}>
                      러저
                    </TableCell>
                    <TableCell align="center" sx={sx.tableheader}>
                      장소
                    </TableCell>
                    <TableCell align="center" sx={sx.tableheader}>
                      인원
                    </TableCell>
                    <TableCell align="center" sx={sx.tableheader}>
                      날짜
                    </TableCell>
                    <TableCell align="center" sx={sx.tableheader}>
                      시간
                    </TableCell>
                    <TableCell align="center" sx={sx.tableheader}>
                      함께하기 요청
                    </TableCell>
                  </TableRow>
                </TableHead>
              </Table>
            </TableContainer>
          </Paper>
        </Grid>
      </Grid>
    </>
  );
};

export default ScheduleManageScreen;
